import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty,
		WucTab
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [],
		};
	},
	
	onLoad(options){
		this.loadNav();
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		// console.log(options)
		// if(options.state){				
		// this.tabCurrentIndex = +options.state;
		// }
		//


		//
		// if(options.state == 0){
		// 	this.loadData()
		// }
		//
		
	},
	 
	methods: {
		loadNav(){
			this.$http.post('/app/page/nav', {nav_types:''}).then(res => {
				if(res.data.items&&res.data.items.items){
					this.navList.push(
						...res.data.items.items
					);
				}
				
			}).catch(err => {});
		},
		//获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			
			this.$http.post('/cms/member/order/list', {orders:'1',maxMoney:0.01,status:state}).then(res => {
				navItem.loadingType = 'noMore';
				if(res.data.items&&res.data.items){
					navItem.orderList.push(
						...res.data.items
					);
					navItem.loadingType = 'more';
				}
				this.$set(navItem, 'loaded', true);
				
			}).catch(err => {});
		}, 
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		}
	},
}
